import React, { FC, useContext, useLayoutEffect ,useEffect, useRef, useState } from 'react'
import { StudentHeader } from '../../components/Header'
import { Link, useParams } from 'react-router-dom'
import { CompanyContext } from '../../store/CompanyContext'
import { RequestContext } from '../../store/RequestContext'
import { AuthContext } from '../../store/AuthContext'
import '../../index.css'
import { Checkbox } from '../../components/formFieldInput'
import $api from '../../http'
import { auto, left } from '@popperjs/core'
 
export const StudentRequestListPage: FC = () => {
  const params = useParams()
  const { requests, nominations, fetchRequests, addRequest, removeRequest } =
    useContext(RequestContext)
  const { companies, fetchCompanies } = useContext(CompanyContext)

  
  function formValue2(event: React.ChangeEvent<HTMLSelectElement>) {
    const arr = nominations.find((e) => e.id === event.target.value)?.subNomination



    setState({...state, nominationRef: event.target.value.trim()});
  }


  const [checked1, setChecked1] = useState(false)
  const [checked2, setChecked2] = useState(false)
  const [checked3, setChecked3] = useState(false)
  const { id, financingSource } = useContext(AuthContext)
  const qs = requests.filter(r => r.company.id === Number(params.id) && r.student.id == id) 
  const [state, setState] = useState({nominationRef: ''});
  const formValue = (event: React.ChangeEvent<HTMLSelectElement>) => {
    setState({...state, [event.target.name]: event.target.value.trim()});
  };
  const [subArray, setSubArray] = useState<{
    user_chose: number
    criterion: number
    id: number
    name: string
    paymentVPO: number
    paymentSPO: number
  }[]>([])
  const nominationRef = useRef<HTMLSelectElement>(null)
  const subNominationRef = useRef(null)
  const createModalRef = useRef(null)
  const request = requests.find(r => r.student.id === Number(params.id) && r.student.id == id)
  const company = companies.find(r => r.id === Number(params.id))

  const handleCheck1 = () => {
    setChecked1(!checked1);
  };
  const handleCheck2 = () => {
    setChecked2(!checked2);
  };
  const handleCheck3 = () => {
    setChecked3(!checked3);
  };  



  useEffect(() => {

    fetchRequests()
    if (companies.length) fetchCompanies()
    // if (
    //   requests
    //     .filter(r => r.company.id === Number(params.id))
    //     .filter(r => r.student.id === id).length === 0
    // )
    // navigate('/companies/')
  }, [ ])
  
  useEffect(() => {
    M.FormSelect.init(nominationRef.current!)
    M.Modal.init(createModalRef.current!)
  }, [requests])




  if (!requests.length) {
    return (
      <>
        <StudentHeader />
        <div className='my-center'>
          <div className='preloader-wrapper big active'>
            <div className='spinner-layer spinner-blue-only'>
              <div className='circle-clipper left'>
                <div className='circle'></div>
              </div>
              <div className='gap-patch'>
                <div className='circle'></div>
              </div>
              <div className='circle-clipper right'>
                <div className='circle'></div>
              </div>
            </div>
          </div>
        </div>
      </>
    )
  }


  return (
    <>
      <StudentHeader />
      <div className='container'>
        <table className='mt-4 striped'>
          <thead>
            <tr>
              <th>Критерий</th>
              <th>Статус</th>
              <th>Институт</th>
              <th>Направление</th>
              <th>Курс</th>
            </tr>
          </thead>
          <tbody>

            {qs?.map(r => {
  
              return (
                <tr key={r.id}>
                  <td>{r.nomination.name}</td>
                  <td>{r.status}</td>
                  <td>{r.student.institute}</td>
                  <td>{r.student.direction}</td>
                  <td>{r.student.course}</td>
                  <td>
                    <Link
                      className='waves-effect light-blue btn-small'
                      to={`/requests/${r.id}/`}
                    >
                      Редактировать
                    </Link>
                  </td>
                
                    <td>
                    
                    <a
                    className='waves-effect red darken-3 btn-small btn'
                    onClick={() => {
                      if (r.status == "Черновик") {
                      removeRequest(r.id.toString())
                      fetchRequests() 
                      fetchCompanies()
                      fetchRequests()
                      } else {
                        alert('Удалять заявление можно при статусе Черновик')
                      }
                    }}
                  >
                    Удалить
                  </a>
                  </td>
                </tr>
              )
            })}
          </tbody>
        </table>
        <div className='div-checkbox' style={{
          margin: "2% 0%",
          float: 'left'
        }}>
          <span>Подавая заявку, вы подтверждаете следующее:</span>
          <br/>
            <Checkbox
                label="Даю согласие в установленном Федеральным законом от 27.07.2006 № 152-ФЗ «О персональных данных» порядке на обработку и использование моих персональных данных "
                onChange={handleCheck1}
            />
            <br />
            <br />

            <Checkbox
                label='Предупрежден об ответственности за представление заведомо ложных и (или) недостоверных сведений, а равно путем умолчания о фактах, влекущих прекращение указанных выплат, в соответствии со ст. 159.2. УК РФ от 13.06.1996 N 63-ФЗ (ред. от 18.02.2020)'
                onChange={handleCheck2}
          />
              <br />
              <br />

            <Checkbox
                label='Обязуюсь предоставить оригиналы прилагаемых документов по запросу сотрудника Центра социально-экономической поддержки студентов в течение 10 рабочих дней с момента поступления запроса в мой личный кабинет. Уведомлен, что в случае непредставления информации заявка аннулируется'  
                onChange={handleCheck3}
            />

<div
          style={{
            float: 'left',
            marginTop: 36,
          }}
        >
          <button
            className='btn light-blue darken-2 waves-effect waves-light'
            onClick={() => {
              
             if (company?.endDate !== undefined && company?.endDate < new Date()){
                alert('Кампания окончена!')
            }else if(checked1 && checked2 && checked3 == true){
                const i = M.Modal.getInstance(createModalRef.current!)
                i.open()
              } else {

                alert('Вы не отметили обязательные поля!')
              }

              
            }}
          >
            <i className='material-icons left'>add</i>
            Добавить заявку
          </button>
        </div>
        </div>
        
      </div>

      {/* create modal */}
      <div ref={createModalRef} id='criterion-div' className='modal'>
        <div className='modal-content'>
          <h4>Создать заявку</h4>
          <div className='input-field col s12'>
            <select  
            onChange={(event) => {
              formValue(event)
              const nom = nominations.find((n) =>  parseInt(n.id, 10) === parseInt(event.target.value, 10))?.sub_criterion
              if (nom === undefined){
                setSubArray([])
              }else{
                setSubArray(nom)
              }
            }}
            name='nominationRef'
            value={state.nominationRef}           
            ref={nominationRef}>
              <option>Выберите критерий</option>
              {/* <option>Выберите критерий</option> */}
              {nominations
              
                .filter(
                  n => {

                    if (financingSource === 'Полное возмещение затрат') {
                    const paymentStatus = n.payment_status === true 
                    const studentFinanceSource = financingSource === 'Полное возмещение затрат'
                    
                    return !(qs.map(r => r.nomination.name).indexOf(n.name) + 1) && paymentStatus && studentFinanceSource
                        } else {
                      return !(qs.map(r => r.nomination.name).indexOf(n.name) + 1)
                    }
                    
                  }
                     
                ).map(n => (   
                  <option value={n.id} key={n.id}>
                    
                    {n.name}
 
                  </option>
                ))
                  }              
              
            </select>
            
            <label>Критерий</label>


            
          </div>

          { subArray.length !== 0 ?
            <div>
              <label>Выберите Подкритерий</label>
                  <select ref={subNominationRef} style={{display: 'block'}} >
                        
                        { subArray.map(sub => (
                          <option value={sub.id} key={sub.id}>{sub.name}</option>
                          ))}     
                      
                  </select>
            </div> : <div></div>
            
            }

        </div>

        <div className='modal-footer'>

        
          <button
            className='btn light-blue darken-2 waves-effect waves-light'
            onClick={() => {
              
              // @ts-ignore
              const nom = nominationRef.current!.value
              // @ts-ignore
              const subCriterion = subNominationRef?.current?.value !== undefined
              if (subCriterion) {
                // @ts-ignore
              addRequest(Number(params.id), id, nom, subNominationRef?.current?.value)
              }else{
                
                addRequest(Number(params.id), id, nom, 1)
              }
              const i = M.Modal.getInstance(createModalRef.current!)
              i.close()
            }}
          >
            <i className='material-icons left'>save</i>
            Создать заявку
          </button>
        </div>
      </div>
    </>
  )
}
